/* color-pallete */

:root {
  --maxident: #e47cad;
  --maxident-opacity: rgba(228, 124, 173, 0.5);
  --oddinary: #04bb21;
  --oddinary-opacity: rgba(4, 187, 19, 0.5);
  --noeasy: #1a0ff4;
  --noeasy-opacity: rgba(15, 34, 244, 0.5);
}

.maxident-era {
  background-color: var(--maxident);
  color: var(--maxident-bright);
}

.maxident-to-oddinary {
  background: linear-gradient(to bottom, var(--maxident) 60%, var(--oddinary));
}

.oddinary-era {
  background-color: var(--oddinary);
  color: var(--oddinary-bright);
}

.oddinary-to-noeasy {
  background: linear-gradient(to bottom, var(--oddinary) 60%, var(--noeasy));
}

.noeasy-era {
  background-color: var(--noeasy);
  color: var(--noeasy-bright);
}

/*! blocks styles */
.block-wrapper {
  width: 100%;
}

.block-body {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
}

/* ! picture style */

.picture {
  max-width: 400px;
  height: 100%;
  margin: 10px;
}

/* decorative elements */
.vl {
  width: 6px;
  height: 100%;
  display: inline-grid;
  left: 50%;
}

.point-wrapper {
  position: absolute;
  margin-left: -22px;
  top: 50%;
}

.point {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  display: block;
}

/* text elements */
.date-wrapper {
  display: inline-grid;
  margin: 20px -80px 10px 0;
}

.date-text {
  font-size: 50px;
  margin-left: 235px;
}

.date-text:hover {
  cursor: pointer;
  text-shadow: 2px 2px 3px aliceblue;
}

.event-wrapper {
  display: inline-grid;
  max-width: 450px;
  margin: 30px 0 20px -60px;
  justify-self: baseline;
}

.event-text {
  font-size: 25px;
  padding-bottom: 20px;
}
