/* styles reset */

*, *::before, *::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
html, body {
  height: 100%;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
input, button, textarea, select {
  font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
#root, #__next {
  isolation: isolate;
}

/* main styles */

.point-wrapper {
  position: sticky;
  z-index: 50;
}

.point {
  background-color: blueviolet;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  margin: 0 50%;
}